import React from 'react';
import {Button, StringContainer, MySymbol, MyApp} from "./style"
const MainComponent = (props) =>{
		const {
			timePassed, numberOfMistakes, strLength,
			arrayWithTrue, arrayWithMistakes, appState,
			startMyApp, restartMyApp, string
		} = props
    const ResultElem = ()=> (
  		<div>
				<p> :  {timePassed}</p>
				<p> :  {numberOfMistakes} </p>
				<p> :  {strLength}</p>
				<p> : {strLength - arrayWithTrue.length - arrayWithMistakes.length}</p>
  		</div>
  	)
		
	return(	
			<MyApp>
				{(()=>{
					switch(appState){
	      		case 2:
	      			return (
	      				<div>
							<p> :  {60 - timePassed}</p>
							<ResultElem/>
							<br/>

							<StringContainer>
								{string.split('').map((elem,i) => arrayWithTrue.includes(i)?null:  <MySymbol className={arrayWithMistakes.includes(i)?'mistake':''} key={i}>{elem}</MySymbol>
									
								)}
							</StringContainer>
						</div>
						)
	      		case 3:
	      			return <ResultElem/>
					}
				})()} 

				{(()=>{
					switch(appState){
						case 1:
							return <Button onClick={()=>startMyApp()}></Button>
	      		case 2:
	      			return <Button onClick={restartMyApp}></Button>
	      		case 3:
	      			return <Button onClick={startMyApp}></Button>
					}
				})()} 
	   
			</MyApp>
	)
}
export default MainComponent


